import React from 'react';

function ProgressDot(props) {
    const { href = "", title = "", complete = false } = props
    return (
        <li className={complete? "completed" : ""}>
            <a href={`${href}`} data-toggle="tooltip" data-placement="bottom" title={`${title}`} >
                <span className="bubble"></span>
            </a>
        </li>
    )
}

function ProgressIndicator(props) {
    const { indicatorData = [] } = props;
    return (
        <ul className="progress-indicator">
            {
                indicatorData.map(item => 
                <ProgressDot title={`${item.title}`} href={`${item.href}`} complete={item.complete} />)
            }
        </ul>
    )
}

export default ProgressIndicator