import React, { Component } from 'react';
import './StepFour.css';
import WebsiteView from '../../../commonComponents/WebisiteView/webisiteView';
import { storageCacheKey } from '../../../config/config';
import fetchClient from '../../../helper/fetchClient';

class StepFour extends Component {
  // constructor(props){
  // super(props);
  // this.state = {};
  // }

  // componentWillMount(){}
  // componentDidMount(){}
  // componentWillUnmount(){}

  // componentWillReceiveProps(){}
  // shouldComponentUpdate(){}
  // componentWillUpdate(){}
  // componentDidUpdate(){}

  constructor(props) {
    super(props);
    this.props.parentCallback(4);
    this.state = {
      widget_phone: '',
      widget_id: '',
      page_number: '4'
    }
  }


  //saving the values in the cache
  saveIntoCache = (key, value) => {
    localStorage.setItem(key, value);
  }

  //retrieve the values from cache
  retrieveFromCache = (key) => {
    return localStorage.getItem(key);
  }

  //validate the cache on pageLoad
  validateCacheData = () => {

    //@todo remove the token addition manually.

    let widget_id = this.state.widget_id;
    console.log("Widget : ", widget_id);
    if (!widget_id) {
      console.log("Widget 2 : ", widget_id);
      widget_id = this.retrieveFromCache(storageCacheKey.WIDGET_KEY);
      if (widget_id) {

        // Found data from cache load the rest of data and hit the apis to update the values
        let widget_phone = this.retrieveFromCache(storageCacheKey.WIDGET_PHONE);

        this.setState({
          widget_id: widget_id,
          widget_phone: widget_phone,
        });

      } else {
        // Didn't find the values so keep everything bank and hit apis for creating the widget
        this.setState({ widget_created: true });
        this.setState({
          widget_id: '',
          widget_phone: '',
        });
      }
    }

    console.log("Validation done")
  }

  // Saving state data for this page to the cache
  saveStateDataToCache = () => {
    try {
      this.saveIntoCache(storageCacheKey.WIDGET_PHONE, this.state.widget_phone);
      return true;
    } catch (error) {
      return false
    }
  }

  //b10279b3-44dd-2d4d-0870-8498c74d3690

  componentWillMount() {
    // setting up token in the state so that we can make the utilze of it.
    this.validateCacheData();
    console.log("Widget Calls");
    console.log(this.state);
  }

  updateWidget = () => {
    try {
      let data = {
        "phone": this.state.widget_phone
      }

      let widgetId = this.state.widget_id;

      console.log( widgetId);

      fetchClient.put(`/services/widgets/step_save/${widgetId}`, data, {}).then((response) => {
        console.log("Updated")
        console.log(response.data);
        if (response.data) {
          let widgetData = response.data;
          if (widgetData.uid && widgetData.hashkey) {
            this.setState({
              widget_created: true,
              widget_id: widgetData.hashkey || '',
              widget_phone: widgetData.phone || ''
            })
            if (this.saveStateDataToCache()) {
              if (this.state.widget_created) {
                // redirect to next page
                console.log("Widget Created : Saved");
                this.props.pageRedirect(5);
              } else {
                console.log("Widget Created : Not Saved");
              }
            }
          } else {
            console.log("Widget Created : Failed");
          }
        }

        // then save the data into cache and update the page to next one
      }).catch((error) => {
        console.log("API Error occured.")
        console.log(error);
      })

    } catch (error) {
      console.log("Some  with acios error occured");
      console.log(error)
    }
  }

  handleChange = (e) => {
    let stateKey = e.target.id;
    let stateValue = e.target.value;
    console.log(stateKey, " : ", e.target.value);

    if (stateKey == 'widget_phone') {
      this.setState({
        'widget_phone': `${stateValue}`
      });
    }
  }

  render() {
    return (
      <div className="row">
        <div className="col-lg-12">
          <div className="text-center body-content-area">
            <h1>Congrats, <span>Sanjib!</span> Wondrous widget. <br />
            Now let’s make it ring! <br />
            Enter your mobile number.</h1>
            <div className="content-area-section m-t-30">
              <form className="form-horizontal enter-website custom-form  enter-mobile" action="index.html">
                <div className="row">
                  <div className=" col-md-8 col-xs-6">
                    <div className="phone-number-w">
                      <div className="phone-number">
                        <input id="widget_phone" name="widget_phone" type="tel" className="form-control " pattern="\d{3}[\-]\d{3}[\-]\d{4}" onChange={this.handleChange} />
                        <svg className="checkmark" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 52 52">
                          <circle className="checkmark__circle" cx={26} cy={26} r={25} fill="none" />
                          <path className="checkmark__check" fill="none" d="M14.1 27.2l7.1 7.2 16.7-16.8" /></svg>
                      </div>
                      <label>Enter your 10-digit mobile number.
                    </label>
                    </div>
                  </div>
                  <div className=" col-md-4 col-xs-6">
                    <div className="c-otp-main">
                      <p className="c-otp-before">
                        We will send you a 4-digit PIN via SMS.
                    </p>
                      <div className="c-otp-w">
                        <div className="c-otp-in">
                          <span> <input type="text" maxLength={1} /> </span>
                          <span> <input type="text" maxLength={1} /></span>
                          <span> <input type="text" maxLength={1} /></span>
                          <span> <input type="text" maxLength={1} /></span>
                        </div>
                        <p> Great, now take the 4-digit PIN from your SMS text and enter here.
                      </p>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="text-center">
                  <button type="submit" id="toggle" className="btn btn-lg  m-t-20 sign-in do-it e  btn-active" onClick={this.updateWidget}>Verify</button>
                </div>
                <WebsiteView />
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default StepFour;