import React, { Component } from 'react';
import './StepFive.css';
import WebsiteView from '../../../commonComponents/WebisiteView/webisiteView';
import { storageCacheKey } from '../../../config/config';
import fetchClient from '../../../helper/fetchClient';

class StepFive extends Component {
  // constructor(props){
  // super(props);
  // this.state = {};
  // }

  // componentWillMount(){}
  // componentDidMount(){}
  // componentWillUnmount(){}

  // componentWillReceiveProps(){}
  // shouldComponentUpdate(){}
  // componentWillUpdate(){}
  // componentDidUpdate(){}

  // {{request_url}}/services/widgets/pixel/generate/{{userhashkey}}/{{widgethashkey}}


  constructor(props) {
    super(props);
    this.props.parentCallback(5);
    this.state = {
      widget_id: '',
      page_number: '3',
      widget_code: ''
    }
  }

  //saving the values in the cache
  saveIntoCache = (key, value) => {
    localStorage.setItem(key, value);
  }

  //retrieve the values from cache
  retrieveFromCache = (key) => {
    return localStorage.getItem(key);
  }

  //validate the cache on pageLoad
  validateCacheData = () => {

    //@todo remove the token addition manually. 
    let widget_id = this.state.widget_id;
    console.log("Widget : ", widget_id);
    if (!widget_id) {
      console.log("Widget 2 : ", widget_id);
      widget_id = this.retrieveFromCache(storageCacheKey.WIDGET_KEY);
      if (widget_id) {
        this.setState({
          widget_created: true,
          widget_id: widget_id,
        });
        console.log("Set status");
        console.log(this.state)
      } else {
        // Didn't find the values so keep everything bank and hit apis for creating the widget
        this.setState({ widget_created: true });
        this.setState({
          widget_created: false,
          widget_id: '',
        });
      }
    }
    console.log("Validation done");
  }

  componentWillMount() {
    // setting up token in the state so that we can make the utilze of it.
    this.validateCacheData();
    console.log("Widget Calls");
    console.log(this.state);
    this.getPixelCode();
  }

  componentDidMount() {
    // setting up token in the state so that we can make the utilze of it.
    console.log("Widget Calls: component mounted");
    console.log(this.state);
   
  }

  getPixelCode = async () => {
    try {
      let data = {}
      let widgetId = this.state.widget_id;
      let responseCode = await fetchClient.get(`/services/widgets/pixel/generate/${widgetId}`, data, {});
      if (responseCode.data) {
        let widgetData = responseCode.data;
        if (widgetData.msg && widgetData.code) {
          let widgetCode = widgetData.code;
          this.setState({
            widget_code: widgetCode
          });
          console.log(widgetCode);
        }
      } else {
        console.log("Widget Created : Failed");
      }
      // then save the data into cache and update the page to next one
    } catch (error) {
      console.log("Some  with acios error occured");
      console.log(error)
    }
  }


  render() {
    return (
      <div className="row">
        <div className="col-lg-12">
          <div className="text-center body-content-area">
            <h1>It's showtime, <span>Sanjib!</span> To make tok <br />widget live, apply the pixel code
              on<br />your page. That’s it!</h1>
            <div className="content-area-section m-t-30">
              <div id="emitter" />
              <form className="form-horizontal enter-website custom-form" action="index.html">
                <div className="check-icon">
                  <div className="row">
                    <div className="col-sm-12 col-lg-12 col-md-12">
                      <pre id="divClipboard">
                        {""}
                        <code>
                          {"\n"}
                          {this.state.widget_code}
                          {"\n"}
                        </code>
                        {""}
                      </pre>
                    </div>
                  </div>
                </div>
                <div className="row">
                  <div className="col-sm-12 col-lg-12 col-md-12">
                    <div className="form-group ">
                      <div className="text-center">
                        <a id="toggle" className="btn btn-lg  m-t-20 sign-in do-it btn-botton-line btn-active copyInput" title="content" onclick="copyClipboard()">Copy to clipboard</a>
                        &nbsp;
                        <a id="toggle" className="btn btn-lg  m-t-20 sign-in do-it btn-botton-line btn-active special-link">Pixel
                          added on my page</a>
                      </div>
                    </div>
                    <WebsiteView />
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default StepFive;