//common rules for form

export const emailRules = () => [
    {
        required: true,
        message: 'Please input your Email!',
    },
    {
        type: 'email',
        message: 'The input is not valid email!',
    }
]


export const passwordRules = (textLimit = 8, label = "password") => [
    {
        required: true,
        message: `Please input your ${label}!`,
    },
    {
        min: textLimit,
        message: `minimum ${textLimit} letters`
    }
]