import React, { Component } from 'react';
// import './tokTownMain.scss'
import { Switch, Route, Redirect } from 'react-router-dom'
import AuthMain from '../AuthMain/AuthMain';

import WidgetCreation from '../WidgetCreation/widgetCreation';
import { isCompositeComponent } from 'react-dom/test-utils';

class TokTownMain extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }
    render() {
        return (
            <Switch>
                <Route exact path={["/dashboard"]}>
                    <Redirect to={"/dashboard/enter-website"} />
                </Route>
                <Route path={"/dashboard/widget"}
                    children={<WidgetCreation />} />
                <AuthMain />
            </Switch>
        );
    }
}

export default TokTownMain;