// importing core modules
import React, { Component } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import Amplify, { Auth, Hub } from "aws-amplify";

// importing sessions and cache keys
import { storageCacheKey } from "../../config/config";

// importing headers
import LoginPageHeader from "../../commonComponents/LoginPageHeader/LoginPageHeader";

// importing pages
import SignUp from "../../components/SignUp/signUp";
import ForgotPassword from "../../components/ForgotPassword/forgotPassword";
import ResetPassword from "../../components/ResetPassword/resetPassword";
import SignIn from "../../components/SignIn/signIn";

// importing loaders
import Loading from "../../componentsModule/loaderProgress/loaderProgress";

import history from "../../utils/history";
import { data } from "jquery";

class AuthMain extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loadingIsActive: false,
      loadingMessage: "",
      // Signup Variables
      signupName: "",
      signupEmail: "",
      signupPassword: "",
      // Signin Variables
      loginEmail: "",
      loginPassword: "",
      // reset password email
      resetEmail: "",
      resetEmailCode: "",
      resetEmailPassword: "",
      resetEmailCodeSent: false,
      // for redirect
      redirectToRoute: false,
      redirectToUrl: "",
      //for login signup password error
      validationError: false,
      validationErrorMessage: "",
    };

    this.textboxCallback = this.handleChange.bind(this);
    this.accountAccessCallback = this.accountAccessRequest_Event.bind(this);
  }

  componentDidMount() {
    // checking for token in localhost
    console.log("KEY", this.retrieveFromCache(storageCacheKey.USER_ID));
    if (this.retrieveFromCache(storageCacheKey.USER_ID)) {
      this.setState({
        redirectToRoute: true,
        redirectToUrl: "/dashboard/widget/step-one",
      });
      // history.push("/dashboard/widget");
    }
  }

  showLoader = (show, message) => {
    this.setState({
      loadingIsActive: show,
      loadingMessage: message,
    });
  };

  /**
   * COdes for signin methods
   * @param {} key
   * @param {*} value
   */

  // for handling all of the changes of the textbox of login and signup pages
  handleChange = (e) => {
    let stateKey = e.target.id;
    let stateValue = e.target.value;
    // console.log(stateKey, " : ", stateValue);
    if (stateKey == "login-Email") {
      this.setState({
        loginEmail: `${stateValue}`,
      });
    } else if (stateKey == "login-Password") {
      this.setState({
        loginPassword: `${stateValue}`,
      });
    } else if (stateKey == "signup-Name") {
      this.setState({
        signupName: `${stateValue}`,
      });
    } else if (stateKey == "signup-Email") {
      this.setState({
        signupEmail: `${stateValue}`,
      });
    } else if (stateKey == "signup-Password") {
      this.setState({
        signupPassword: `${stateValue}`,
      });
    } else if (stateKey == "emailReset-Password") {
      this.setState({
        resetEmail: `${stateValue}`,
      });
    } else if (stateKey == "resetNew-Code") {
      this.setState({
        resetEmailCode: `${stateValue}`,
      });
    } else if (stateKey == "resetNew-Password") {
      this.setState({
        resetEmailPassword: `${stateValue}`,
      });
    }
    // console.log(this.state)
  };

  accountAccessRequest_Event = (e, method) => {
    e.preventDefault();
    if (method == "generic_signin") {
      this.generic_signin(e);
    } else if (method == "generic_signup") {
      this.generic_signup(e);
    } else if (method == "gmail_signin") {
      this.gmailAuth(e);
    } else if (method == "facebook_signin") {
      this.facebookAuth(e);
    } else if (method == "password_reset") {
      this.forgotPassword(e);
    } else if (method == "password_reset_code") {
      this.forgetPasswordVerifyCode(e);
    }
  };

  ////////////////////////// START OF AUTH SECTION //////////////////////////

  // for normal signin using email and password
  generic_signin = (event) => {
    event.preventDefault();
    this.showLoader(true, "Logging in Please wait.");
    const { loginEmail, loginPassword } = this.state;
    console.log(loginEmail, loginPassword);
    Auth.signIn({
      username: loginEmail,
      password: loginPassword,
    })
      .then((data) => {
        console.log("Inside of auth setion signin : ", data);
        this.showLoader(true, "Setting up your dashboard.");
        this.verifyAuthUser();
      })
      .catch((err) => {
        this.showLoader(false, "");
        let errorMessage = "Invalid login/ password. Please try again.";
        console.log(err);
        this.setState({
          validationError: true,
          validationErrorMessage: errorMessage,
        });
      });
  };

  // for normal siginup using email and password
  generic_signup = (event) => {
    event.preventDefault();
    this.showLoader(true, "Creating account! Please wait.");
    const { signupName, signupEmail, signupPassword } = this.state;
    Auth.signUp({
      username: signupEmail,
      password: signupPassword,
      attributes: {
        name: signupName,
        email: signupEmail,
      },
      validationData: [],
    })
      .then((data) => {
        console.log(data);
        this.showLoader(true, "Account Created.");
        // alert("Sign up Success!. Login to continue.", data);
        console.log("Getting signed in. ");
        // const { signupEmail, signupPassword } = this.state;
        this.showLoader(true, "Setting up account.");
        Auth.signIn({
          username: signupEmail,
          password: signupPassword,
        })
          .then((data) => {
            console.log("Inside of auth setion signin : ", data);
            this.verifyAuthUser();
          })
          .catch((err) => {
            this.showLoader(false, "");
            let errorMessage = "Invalid login/ password. Please try again.";
            console.log(err);
            this.setState({
              validationError: true,
              validationErrorMessage: errorMessage,
            });
          });
      })
      .catch((err) => {
        this.showLoader(false, "");
        let errorMessage = "Signup Failed. Please try again.";
        console.log(err);
        this.setState({
          validationError: true,
          validationErrorMessage: errorMessage,
        });
      });
  };

  // for social facebook login
  facebookAuth = (e) => {
    e.preventDefault();
    this.props.socialLoginCallback("facebook");
  };

  // for social gmail login
  gmailAuth = (e) => {
    e.preventDefault();
    this.props.socialLoginCallback("gmail");
  };

  ////////////////////////// END OF AUTH SECTION //////////////////////////

  ////////////////////////// START OF ACCOUNT EDIT SECTION //////////////////////////

  // for sending the password reset link to the mail of the user
  forgotPassword = (event) => {
    event.preventDefault();
    this.showLoader(true, "Sending password reset link! Please wait.");
    let username = this.state.resetEmail;
    Auth.forgotPassword(username)
      .then((data) => {
        this.showLoader(false, "");
        console.log("executed : ");
        console.log(data);
        this.setState({
          resetEmailCodeSent: true,
        });
      })
      .catch((err) => {
        this.showLoader(false, "");
        let errorMessage = "Some error occured! Try again";
        console.log(err);
        this.setState({
          validationError: true,
          validationErrorMessage: errorMessage,
        });
      });
  };

  forgetPasswordVerifyCode = (event) => {
    event.preventDefault();
    this.showLoader(true, "Verifying code! Please wait.");
    let username = this.state.resetEmail;
    let code = this.state.resetEmailCode;
    let new_password = this.state.resetEmailPassword;
    Auth.forgotPasswordSubmit(username, code, new_password)
      .then((data) => {
        console.log("Password reset Code Verify : Success ", data);
        alert("Your password has been updated! Please login to continue");
        this.showLoader(false, "");
        this.redirectToLogin();
      })
      .catch((err) => {
        this.showLoader(false, "");
        let errorMessage = "Some error occured! Try again";
        console.log(err);
        this.setState({
          validationError: true,
          validationErrorMessage: errorMessage,
        });
      });
  };

  // when user is logged in
  changePassword = ({ oldPassword, newPassword }) => {
    Auth.currentAuthenticatedUser()
      .then((user) => {
        return Auth.changePassword(user, oldPassword, newPassword);
      })
      .then((data) => {
        console.log(data);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  ////////////////////////// END OF ACCOUNT EDIT SECTION //////////////////////////

  ////////////////////////// START OF SESSION MANAGEMENT SECTION //////////////////////////

  //saving the values in the cache
  saveIntoCache = (key, value) => {
    localStorage.setItem(key, value);
  };

  //retrieve the values from cache
  retrieveFromCache = (key) => {
    return localStorage.getItem(key);
  };

  //redirecting the login function
  redirectToLogin() {
    // this.props.history.push('/sign-in');
    this.showLoader(false, "");
    this.setState({
      redirectToRoute: true,
      redirectToUrl: "/sign-in",
    });
    // history.push('/sign-in');
  }

  //redirecting the dashboard function
  redirectToDashboard() {
    // this.props.history.push('/dashboard');
    this.showLoader(false, "");
    this.setState({
      redirectToRoute: true,
      redirectToUrl: "/dashboard/widget/step-one",
    });
    // history.push('/dashboard');
  }

  //saving the token to the cache
  getTokenAndSave() {
    Auth.currentSession()
      .then((res) => {
        console.log("inside of session section of user ", data);
        let accessToken = res.getAccessToken();
        let jwt = accessToken.getJwtToken();
        this.saveIntoCache(storageCacheKey.USER_TOKEN, jwt);
        this.redirectToDashboard();
      })
      .catch((err) => {
        this.showLoader(false, "");
        console.log("User session expired");
        this.redirectToLogin();
      });
  }

  // verifying the auth token of a user
  verifyAuthUser() {
    Auth.currentAuthenticatedUser({
      bypassCache: false, // Optional, By default is false. If set to true, this call will send a request to Cognito to get the latest user data
    })
      .then((user) => {
        console.log("inside of auth section of user ", data);
        this.getTokenAndSave();
      })
      .catch((err) => {
        this.showLoader(false, "");
        console.log("User not logged in , Error : ", err);
        this.redirectToLogin();
      });
  }

  ////////////////////////// END OF SESSION MANAGEMENT SECTION //////////////////////////

  redirectAfterAction() {
    if (this.state.redirectToRoute) {
      return <Redirect to={this.state.redirectToUrl} />;
    }
  }

  render() {
    return (
      <div className="dash-bord-body">
        {this.redirectAfterAction()}
        <LoginPageHeader />

        <Loading
          loading={this.state.loadingIsActive}
          background="rgba(115, 115, 115, 0.31)"
          loaderColor1="rgba(239, 0, 0, 0.48)"
          loaderColor2="rgba(59, 198, 255, 0.89)"
          message={this.state.loadingMessage}
        />

        <Switch>
          <Route exact path={["/"]}>
            <Redirect to={"/sign-in"} />
          </Route>
          <Route
            exact
            path={"/sign-in"}
            render={() => <SignIn
              textboxCallback={this.handleChange}
              accountAccessCallback={this.accountAccessRequest_Event}
              loginError={this.state.validationError}
              loginErrorMessage={this.state.validationErrorMessage}
            />
            }
          />
          <Route
            exact
            path={"/sign-up"}
            children={
              <SignUp
                textboxCallback={this.handleChange}
                accountAccessCallback={this.accountAccessRequest_Event}
                signupError={this.state.validationError}
                signupErrorMessage={this.state.validationErrorMessage}
              />
            }
          />
          <Route
            exact
            path={"/forgot-password"}
            children={
              <ForgotPassword
                textboxCallback={this.handleChange}
                accountAccessCallback={this.accountAccessRequest_Event}
                codeSent={this.state.resetEmailCodeSent}
                forgotPassError={this.state.validationError}
                forgotPassErrorMessage={this.state.validationErrorMessage}
              />
            }
          />
          <Route
            exact
            path={"/reset-password"}
            children={<ResetPassword loaderCallBack={this.showLoader} />}
          />
        </Switch>
      </div>
    );
  }
}

export default AuthMain;
