import React, { Component } from 'react';
import './widgetCreation.scss'
import { Switch, Route } from 'react-router-dom'
import WidgetCreationHeader from '../../commonComponents/WidgetCreationHeader/widgetCreationHeader';
import WidgetCreationFooter from '../../commonComponents/WidgetCreationFooter/widgetCreationFooter';
import WidgetCreationOutline from '../../components/WidgetCreationOutline/widgetCreationOutline';
import ProgressIndicator from '../../commonComponents/ProgressIndicator/ProgressIndicator';
import { Redirect } from 'react-router-dom'
import { storageCacheKey } from '../../config/config';
import CssLoader from '../../components/CssLoader/CssLoader';
/**
 * Importing pages for widzard
 */

import StepOne from '../../components/Wizard/StepOne';
import StepTwo from '../../components/Wizard/StepTwo';
import StepThree from '../../components/Wizard/StepThree';
import StepFour from '../../components/Wizard/StepFour';
import StepFive from '../../components/Wizard/StepFive';

// importing loaders
import Loading from '../../componentsModule/loaderProgress/loaderProgress';

class WidgetCreation extends Component {

    constructor(props, context) {
        super(props, context);
        this.state = {
            loadingIsActive: false,
            loadingMessage: '',
            indicatorData: [
                { href: "step-one", title: "Step one", complete: false },
                { href: "step-two", title: "Step two", complete: false },
                { href: "step-three", title: "Step three", complete: false },
                { href: "step-four", title: "Step four", complete: false },
                { href: "step-five", title: "Step five", complete: false }
            ],
            currentState: 1,
            redirect: false,
            page_number: 1
        };

        this.showLoaderCallback = this.showLoader.bind(this);

    }

    showLoader = (show, message) => {
        this.setState({
            loadingIsActive: show,
            loadingMessage: message
        });
    }

    callbackFunction = (childData) => {
        this.setState({ currentState: childData });
        let currentPage = this.state.indicatorData;
        for (let i = 1; i < currentPage.length; i++) {
            currentPage[i - 1].complete = true;
            if (childData == i) {
                break;
            }
        }
        this.setState({
            indicatorData: currentPage
        });
    }

    //saving the values in the cache
    saveIntoCache = (key, value) => {
        localStorage.setItem(key, value);
    }

    //retrieve the values from cache
    retrieveFromCache = (key) => {
        return localStorage.getItem(key);
    }

    savePageToCache = (page_number) => {
        this.saveIntoCache();
    }

    nextRedirect = (page_number) => {
        console.log("Redirect callback called")
        this.setState({
            page_number: page_number,
            redirect: true
        })
        // console.log(_this);

    }

    renderRedirect = () => {
        if (this.state.redirect) {
            let page_number = this.state.page_number;
            if (page_number == 1) {
                return <Redirect to='/dashboard/widget/step-one' />
            } else if (page_number == 2) {
                return <Redirect to='/dashboard/widget/step-two' />
            } else if (page_number == 3) {
                return <Redirect to='/dashboard/widget/step-three' />
            } else if (page_number == 4) {
                return <Redirect to='/dashboard/widget/step-four' />
            } else if (page_number == 5) {
                return <Redirect to='/dashboard/widget/step-five' />
            }
        }
    }

    render() {
        console.log(this.state.currentState);

        return (
            <div className="dash-bord-body">
                <CssLoader />
                <WidgetCreationHeader />

                <Loading
                    loading={this.state.loadingIsActive}
                    background="rgba(115, 115, 115, 0.31)"
                    loaderColor1="rgba(239, 0, 0, 0.48)"
                    loaderColor2="rgba(59, 198, 255, 0.89)"
                    message={this.state.loadingMessage}
                />

                <div className="wrapper">
                    {this.renderRedirect()}
                    <WidgetCreationOutline >
                        <Switch>
                            {/* Custom new routes     */}
                            <Route exact path={"/dashboard/widget/step-one"}
                                children={<StepOne parentCallback={this.callbackFunction} pageRedirect={this.nextRedirect} showLoaderCallback={this.showLoader} />} />
                            <Route exact path={"/dashboard/widget/step-two"}
                                children={<StepTwo parentCallback={this.callbackFunction} pageRedirect={this.nextRedirect} />} />
                            <Route exact path={"/dashboard/widget/step-three"}
                                children={<StepThree parentCallback={this.callbackFunction} pageRedirect={this.nextRedirect} />} />
                            <Route exact path={"/dashboard/widget/step-four"}
                                children={<StepFour parentCallback={this.callbackFunction} pageRedirect={this.nextRedirect} />} />
                            <Route exact path={"/dashboard/widget/step-five"}
                                children={<StepFive parentCallback={this.callbackFunction} pageRedirect={this.nextRedirect} />} />
                            {/* End of custom routes */}
                        </Switch>
                        {/* <WebsiteView position={false} /> */}
                        <ProgressIndicator indicatorData={this.state.indicatorData} />
                    </WidgetCreationOutline>
                </div>

                <WidgetCreationFooter />

            </div>
        );
    }
}

export default WidgetCreation;