import React, { Component } from 'react';
import { withRouter } from 'react-router-dom'
import './ForgotPassword.css'
// import { Form } from 'antd'



class ForgotPassword extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showForgetBlock: true
        }
    }


    GetForgetForm = () => {
        let showCodeBlock = this.props.codeSent;

        if (showCodeBlock) {
            return (
                <form id='code-submit' className="c-login-form" onSubmit={e => this.props.accountAccessCallback(e, 'password_reset_code')}>
                    <div className="from-group">
                        <label htmlFor="Forgot-Email">Email </label>
                        <input type="text" className="form-control" placeholder="johndoe@mojo.com" id='emailReset-Password' name='emailReset-Password' onChange={this.props.textboxCallback} disabled={true} required />
                    </div>
                    <br></br>
                    <label >Enter the code received in email. </label>
                    <div className="from-group">
                        <label htmlFor="Forgot-code">Code </label>
                        <input type="text" className="form-control" placeholder="XXXXXX" id='resetNew-Code' name='resetNew-Code' onChange={this.props.textboxCallback} required />
                    </div>
                    <div className="from-group">
                        <label htmlFor="Forgot-New-Password">New Password </label>
                        <input type="password" className="form-control" placeholder="New Password" id='resetNew-Password' name='resetNew-Password' onChange={this.props.textboxCallback} required />
                    </div>
                    <button type="submit" className="c-login-btn"> Confirm </button>
                </form>
            )
        } else {
            return (
                <form id='email-submit' className="c-login-form" onSubmit={e => this.props.accountAccessCallback(e, 'password_reset')}>
                    <div className="from-group">
                        <label htmlFor="Forgot-Email">Email </label>
                        <input type="email" className="form-control" placeholder="johndoe@mojo.com" id='emailReset-Password' name='emailReset-Password' onChange={this.props.textboxCallback} required />
                    </div>
                    <button type="submit" className="c-login-btn"> Send Email </button>
                </form>
            )
        }
    }


    render() {
        return (
            <section className="c-login-pw c-forget-pw">
                <div className="container">
                    <div className="c-login-in ">
                        <div className="c-row-w no-margin">
                            <div className="c-col-6 no-padding c-login-con-w">
                                <div className="c-login-img">
                                    <picture>
                                        <source srcSet="/images/forget-password.webp" type="image/webp" alt="" />
                                        <source srcSet="/images/forget-password.jpg" type="image/jpg" alt="" />
                                        <img src="/images/forget-password.jpg" alt="" />
                                    </picture>
                                </div>
                                <ul className=" login-other-link">
                                    <li>Do not have an account? <a className="policy" href="sign-up">Sign up here.</a> </li>
                                </ul>
                            </div>
                            <div className="c-col-6 no-padding align-self-center">
                                <div className="c-login-box">
                                    <div className="text-center">
                                        <h2>Forgot password?</h2>
                                        <p>
                                            Please enter your email address to reset your password.
                                        </p>
                                    </div>
                                    {this.GetForgetForm()}
                                    {
                                        // Display Error Message when forgotPassError true
                                        this.props.forgotPassError &&
                                        <div className="alert alert-warning" role="alert">
                                            <b>{this.props.forgotPassErrorMessage}</b>
                                        </div>
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default withRouter(ForgotPassword);