import React, { Component } from 'react';
import './StepOne.css';
import axios from 'axios';
import WebsiteView from '../../../commonComponents/WebisiteView/webisiteView';
import { storageCacheKey, apiURL } from '../../../config/config';
import fetchClient from '../../../helper/fetchClient';

class StepOne extends Component {
  // constructor(props){
  // super(props);
  // this.state = {};
  // }

  // componentWillMount(){}
  // componentDidMount(){}
  // componentWillUnmount(){}

  // componentWillReceiveProps(){}
  // shouldComponentUpdate(){}
  // componentWillUpdate(){}
  // componentDidUpdate(){}

  constructor(props) {
    super(props);
    this.props.parentCallback(1);
    this.state = {
      widget_created: false,
      domain_name: '',
      widget_id: '',
      page_number: '1',
      screenshot_url: '',
      allow_step_two: false,
    }
  }

  //saving the values in the cache
  saveIntoCache = (key, value) => {
    localStorage.setItem(key, value);
  };

  //retrieve the values from cache
  retrieveFromCache = (key) => {
    return localStorage.getItem(key);
  };

  // Display a Modal to conform User Website URL
  confirmUserWebsiteUrl = () => {
    if (window.confirm("Press a button!")) {
      let widget_created = this.state.widget_created;
      if (widget_created) {
        // perform update action
        this.updateWidget();
      } else {
        // perform create action
        this.createWidget();
      }
    } else {
      alert("Please Try Again!");
    }
  };

  //validate the cache on pageLoad
  validateCacheData = () => {
    //@todo remove the token addition manually.
    let widget_id = this.state.widget_id;
    console.log("Widget : ", widget_id);
    if (!widget_id) {
      console.log("Widget 2 : ", widget_id);
      widget_id = this.retrieveFromCache(storageCacheKey.WIDGET_KEY);
      if (widget_id) {
        // Found data from cache load the rest of data and hit the apis to update the values
        let widget_name = this.retrieveFromCache(storageCacheKey.WIDGET_NAME);
        let widget_domain = this.retrieveFromCache(
          storageCacheKey.WIDGET_DOMAIN
        );

        this.setState({
          widget_created: true,
          widget_id: widget_id,
          domain_name: widget_domain,
          widget_name: widget_name,
        });

        console.log("Set status");
        console.log(this.state);
      } else {
        // Didn't find the values so keep everything bank and hit apis for creating the widget
        this.setState({ widget_created: true });
        this.setState({
          widget_created: false,
          widget_id: "",
          domain_name: "",
          widget_name: "",
        });
      }
    }

    console.log("Validation done");
  };

  // Saving state data for this page to the cache
  saveStateDataToCache = () => {
    try {
      this.saveIntoCache(storageCacheKey.WIDGET_KEY, this.state.widget_id);
      this.saveIntoCache(storageCacheKey.WIDGET_NAME, this.state.widget_name);
      this.saveIntoCache(storageCacheKey.WIDGET_DOMAIN, this.state.domain_name);
      this.saveIntoCache(storageCacheKey.PAGE_NUMBER, this.state.page_number);
      return true;
    } catch (error) {
      return false;
    }
  };

  //b10279b3-44dd-2d4d-0870-8498c74d3690

  componentWillMount() {
    // setting up token in the state so that we can make the utilze of it.
    this.validateCacheData();

    console.log("Widget Calls");
    console.log(this.state);
  }

  handleChange = (e) => {
    let stateKey = e.target.id;
    let stateValue = e.target.value;
    console.log(stateKey, " : ", e.target.value);

    if (stateKey == "domain_name") {
      this.setState({
        domain_name: `${stateValue}`,
      });
    }
  };

  updateWidget = () => {
    try {
      let data = {
        name: this.state.domain_name,
        domain: this.state.domain_name,
      };

      let widgetId = this.state.widget_id;

      console.log(widgetId);

      fetchClient
        .put(`/services/widgets/step_save/${widgetId}`, data, {})
        .then((response) => {
          console.log("Updated");
          console.log(response.data);
          if (response.data) {
            let widgetData = response.data;
            if (widgetData.uid && widgetData.hashkey) {
              this.setState({
                widget_created: true,
                widget_id: widgetData.hashkey || "",
                domain_name: widgetData.domain || "",
                widget_name: widgetData.name || "",
                allow_step_two: true,
              });
              if (this.saveStateDataToCache()) {
                if (this.state.widget_created) {
                  // redirect to next page
                  console.log("Widget Created : Saved");
                  this.props.pageRedirect(2);
                } else {
                  console.log("Widget Created : Not Saved");
                }
              }
            } else {
              console.log("Widget Created : Failed");
            }
          }

          // then save the data into cache and update the page to next one
        })
        .catch((error) => {
          console.log("API Error occured.");
          console.log(error);
        });
    } catch (error) {
      console.log("Some  with acios error occured");
      console.log(error);
    }
  };

  createWidget = () => {
    try {
      let data = {
        // "uid": this.state.userToken,
        name: this.state.domain_name,
        domain: this.state.domain_name,
      };

      fetchClient
        .post(`/services/widgets/create`, data, {})
        .then((response) => {
          console.log("Executed");
          console.log(response.data);
          if (response.data) {
            let widgetData = response.data;
            if (widgetData.uid && widgetData.hashkey) {
              this.setState({
                widget_created: true,
                widget_id: widgetData.hashkey || "",
                domain_name: widgetData.domain || "",
                widget_name: widgetData.name || "",
                allow_step_two: true,
              });
              if (this.saveStateDataToCache()) {
                if (this.state.widget_created) {
                  // redirect to next page
                  console.log("Widget Created : Saved");
                  this.props.pageRedirect(2);
                } else {
                  console.log("Widget Created : Not Saved");
                }
              }
            } else {
              console.log("Widget Created : Failed");
            }
          }
          // then save the data into cache and update the page to next one
        })
        .catch((error) => {
          console.log("API Error occured.");
          console.log(error);
        });
    } catch (error) {
      console.log("Some  with acios error occured");
      console.log(error);
    }
  };

  captureWebsite = () => {
    this.props.showLoaderCallback(true, 'Capturing your website. Please hold on.')
    try {
      let data = {
        "domain": this.state.domain_name
      }

      fetchClient.post(`/services/widgets/domain/screen`, data).then((response) => {
        console.log("Executed")
        console.log(response.data);
        if (response.data) {
          let widgetData = response.data;
          console.log('respose : ');
          console.log(widgetData);
          if (widgetData.data) {
            if (widgetData.data.Location) {
              this.setState({
                screenshot_url: widgetData.data.Location,
              })
            } else {
              this.setState({
                screenshot_url: '',
              })
            }
            this.props.showLoaderCallback(false, '');
          }
        }
        // then save the data into cache and update the page to next one
        this.confirmUserWebsiteUrl();
      }).catch((error) => {
        console.log("API Error occured.")
        console.log(error);
        alert("Some error occured");
        this.props.showLoaderCallback(false, '');
        this.confirmUserWebsiteUrl();
      });

    } catch (error) {
      console.log("Some  with acios error occured");
      console.log(error);
      alert("Some error occured");
      this.props.showLoaderCallback(false, '');
    }
  };

  // captureWebsite2 = () => {
  //   let host = "https://www.google.com";
  //   /** Create screenshot options */

  //   // const fs = require('fs')

  //   capture({
  //     url: 'https://twitter.com/',
  //     width: 1024,
  //     height: 768
  //   }).then(screenshot => {
  //     // fs.writeFileSync(`${__dirname}/example.png`, screenshot)
  //     console.log('open example.png')
  //   })

  // }

  onSubmitAction = (event) => {
    event.preventDefault();
    this.captureWebsite();
  };

  render() {
    // console.log(this.state)
    return (
      <div className="row">
        <div className="col-lg-12">
          <div className="text-center body-content-area">
            <h1>
              Hey <span>Sanjib!</span> I’m <span>Tina</span> from TokTown.
              <br />
              Let’s build a terrific tok widget.
              <br />
              Enter your web page url.
            </h1>
            <div className="content-area-section m-t-30">
              <form
                className="form-inline enter-website custom-form"
                action="index.html"
              >
                <div className="enter-website-field clearfix d-flex">
                  <div className=" enter-website-input w-100">
                    <input
                      type="text"
                      className="form-control"
                      id="domain_name"
                      value={this.state.domain_name || ""}
                      placeholder="http://demo.com"
                      onChange={this.handleChange}
                    />
                  </div>
                </div>
                <div className="text-center">
                  {this.state.allow_step_two ? (
                    <a
                      href="step-two"
                      className="btn sign-in do-it btn-botton-line "
                    >
                      Next Step
                    </a>
                  ) : (
                      <a
                        className="btn sign-in do-it btn-botton-line "
                        onClick={this.onSubmitAction}
                      >
                        That's mine
                      </a>
                    )}
                </div>
                <WebsiteView screen_url={this.state.screenshot_url} />
              </form>
            </div>
          </div>
        </div>
        {/* end col */}
      </div>
    );
  }
}

export default StepOne;
