import React, { Component } from 'react';
import './StepThree.css';
import SVG from 'react-inlinesvg';
import WebsiteView from '../../../commonComponents/WebisiteView/webisiteView';
import { storageCacheKey } from '../../../config/config';
import fetchClient from '../../../helper/fetchClient';

class StepThree extends Component {
  // constructor(props){
  // super(props);
  // this.state = {};
  // }

  // componentWillMount(){}
  // componentDidMount(){}
  // componentWillUnmount(){}

  // componentWillReceiveProps(){}
  // shouldComponentUpdate(){}
  // componentWillUpdate(){}
  // componentDidUpdate(){}

  constructor(props) {
    super(props);
    this.props.parentCallback(3);
    this.state = {
      widget_position: '',
      widget_id: '',
      page_number: '3'
    }
  }


  //saving the values in the cache
  saveIntoCache = (key, value) => {
    localStorage.setItem(key, value);
  }

  //retrieve the values from cache
  retrieveFromCache = (key) => {
    return localStorage.getItem(key);
  }

  //validate the cache on pageLoad
  validateCacheData = () => {

    //@todo remove the token addition manually. 
    let widget_id = this.state.widget_id;
    console.log("Widget : ", widget_id);
    if (!widget_id) {
      console.log("Widget 2 : ", widget_id);
      widget_id = this.retrieveFromCache(storageCacheKey.WIDGET_KEY);
      if (widget_id) {

        // Found data from cache load the rest of data and hit the apis to update the values
        let widget_position = this.retrieveFromCache(storageCacheKey.WIDGET_POSITION);

        this.setState({
          widget_id: widget_id,
          widget_position: widget_position,
        });

      } else {
        // Didn't find the values so keep everything bank and hit apis for creating the widget
        this.setState({ widget_created: true });
        this.setState({
          widget_id: '',
          widget_position: '',
        });
      }
    }

    console.log("Validation done")
  }

  // Saving state data for this page to the cache
  saveStateDataToCache = () => {
    try {
      this.saveIntoCache(storageCacheKey.WIDGET_POSITION, this.state.widget_position);
      return true;
    } catch (error) {
      return false
    }
  }

  //b10279b3-44dd-2d4d-0870-8498c74d3690

  componentWillMount() {
    // setting up token in the state so that we can make the utilze of it.
    this.validateCacheData();
    console.log("Widget Calls");
    console.log(this.state);
  }

  updateWidget = () => {
    try {
      let data = {
        "position": this.state.widget_position
      }
      let widgetId = this.state.widget_id;
      console.log(widgetId);

      fetchClient.put(`/services/widgets/step_save/${widgetId}`, data, {}).then((response) => {
        console.log("Updated")
        console.log(response.data);
        if (response.data) {
          let widgetData = response.data;
          if (widgetData.uid && widgetData.hashkey) {
            this.setState({
              widget_created: true,
              widget_id: widgetData.hashkey || '',
              widget_position: widgetData.position || ''
            })
            if (this.saveStateDataToCache()) {
              if (this.state.widget_created) {
                // redirect to next page
                console.log("Widget Created : Saved");
                this.props.pageRedirect(4);
              } else {
                console.log("Widget Created : Not Saved");
              }
            }
          } else {
            console.log("Widget Created : Failed");
          }
        }

        // then save the data into cache and update the page to next one
      }).catch((error) => {
        console.log("API Error occured.")
        console.log(error);
      })

    } catch (error) {
      console.log("Some  with acios error occured");
      console.log(error)
    }
  }

  setPosition(event) {
    console.log(event.target.value);
    let widgetPosition = event.target.value;
    if (widgetPosition && (widgetPosition == 'bottom-right' || widgetPosition == 'bottom-left' || widgetPosition == 'center-right' || widgetPosition == 'center-left')) {
      this.setState({
        widget_position: widgetPosition
      });
    }
  }

  render() {
    return (

      <div className="row">
        <div className="col-lg-12">
          <div className="text-center body-content-area">
            <h1>Looking good, <span>Sanjib!</span> Let’s pick the<br />position of the widget on your page.</h1>
            <div className="content-area-section ">
              <form className="form-horizontal enter-website custom-form">
                <div className="check-icon c-icon-area-position-w">

                  <div className="row" onChange={this.setPosition.bind(this)} >

                    <div className="col-sm-6 col-lg-3 col-md-3">
                      <div className="gal-detail thumb c-active">
                        <h5> Bottom-right</h5>
                        <div className="radio radio-primary">
                          <input className="c-Bottom-right" type="radio" name="position_selector" id="radio1" defaultValue="bottom-right" defaultChecked />
                          <label htmlFor="radio1">
                          </label>
                        </div>
                      </div>
                    </div>

                    <div className="col-sm-6 col-lg-3 col-md-3">
                      <div className="gal-detail thumb">
                        <h5> Bottom-left </h5>
                        <div className="radio radio-primary">
                          <input className="c-Bottom-left" type="radio" name="position_selector" id="radio2" defaultValue="bottom-left" />
                          <label htmlFor="radio2">
                          </label>
                        </div>
                      </div>
                    </div>

                    <div className="col-sm-6 col-lg-3 col-md-3">
                      <div className="gal-detail thumb">
                        <h5> Center-right </h5>
                        <div className="radio radio-primary">
                          <input className="c-Top-right" type="radio" name="position_selector" id="radio3" defaultValue="center-right" />
                          <label htmlFor="radio3">
                          </label>
                        </div>
                      </div>
                    </div>

                    <div className="col-sm-6 col-lg-3 col-md-3">
                      <div className="gal-detail thumb">
                        <h5> Center-left </h5>
                        <div className="radio radio-primary">
                          <input className="c-Top-left" type="radio" name="position_selector" id="radio4" defaultValue="center-left" />
                          <label htmlFor="radio4">
                          </label>
                        </div>
                      </div>
                    </div>

                  </div>
                </div>
              </form>
              <form className="form-horizontal enter-website custom-form" action="index.html">
                <div className="text-center">
                  <a id="toggle" className="btn btn-lg   sign-in do-it btn-botton-line " onClick={this.updateWidget}>Looks good</a>
                </div>
                <div className="website-screen icon-position  m-t-30 clearfix">
                  <WebsiteView />
                  <div className="c-drag-icon">
                    <SVG src="/img/icon5.svg" />
                  </div>
                </div>
              </form>

            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default StepThree;