import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import "./signUp.css";

class SignUp extends Component {
  constructor(props) {
    super(props);
    this.state = {
      passEyeToggle: false, // Toggleing Password EYE active/inactive class
    };
  }

  render() {
    return (
      <section className="c-login-pw c-sign-up-wr">
        <div className="container">
          <div className="c-login-in ">
            <div className="c-row-w no-margin">
              <div className="c-col-6 no-padding c-login-con-w">
                <div className="c-login-img">
                  <picture>
                    <source
                      srcSet="/images/sign-up-img.webp"
                      type="image/webp"
                      alt=""
                    />
                    <source
                      srcSet="/images/sign-up-img.jpg"
                      type="image/jpg"
                      alt=""
                    />
                    <img src="/images/sign-up-img.jpg" alt="" />
                  </picture>
                </div>
                <ul className=" login-other-link">
                  <li>
                    Have an account?{" "}
                    <a className="policy" href="/sign-in">
                      Login here.
                    </a>{" "}
                  </li>
                </ul>
              </div>
              <div className="c-col-6 no-padding">
                <div className="c-login-box">
                  <div className="text-center">
                    <h2>
                      Signup. Let us get <br />
                      you started.{" "}
                    </h2>
                    <p>
                      Welcome. Let us get you a fresh account. This will get you
                      to talk and video-chat freely &amp; effortlessly.{" "}
                    </p>
                  </div>
                  <form
                    className="c-login-form"
                    onSubmit={(e) =>
                      this.props.accountAccessCallback(e, "generic_signup")
                    }
                  >
                    <div className="from-group">
                      <label htmlFor="Signup-Name">
                        Name <span className="mandatory">*</span>
                      </label>
                      <input
                        type="text"
                        className="form-control"
                        placeholder="John Doe"
                        id="signup-Name"
                        name="signup-Name"
                        onChange={this.props.textboxCallback}
                        required
                      />
                    </div>
                    <div className="from-group">
                      <label htmlFor="Signup-Email">
                        Email <span className="mandatory">*</span>
                      </label>
                      <input
                        type="email"
                        className="form-control"
                        placeholder="johndoe@mojo.com"
                        id="signup-Email"
                        name="signup-Email"
                        onChange={this.props.textboxCallback}
                        required
                      />
                    </div>
                    <div className="from-group c-password-wr">
                      <div
                        href=""
                        onClick={(e) => {
                          e.preventDefault();
                          this.setState({
                            passEyeToggle: !this.state.passEyeToggle,
                          });
                        }}
                        className={`eye pull-right ${
                          this.state.passEyeToggle ? "active" : ""
                          } `}
                      ></div>
                      <label htmlFor="Signup-Password">
                        Password <span className="mandatory">*</span>{" "}
                      </label>
                      <input
                        type={`${
                          this.state.passEyeToggle ? "text" : "password"
                          }`}
                        className="form-control"
                        placeholder="At least 8 characters"
                        id="signup-Password"
                        name="signup-Password"
                        onChange={this.props.textboxCallback}
                        required
                      />
                    </div>
                    <button type="submit" className="c-login-btn">
                      {" "}
                      Create Account{" "}
                    </button>
                  </form>
                  <div className="c-login-other">
                    <h5>
                      <span>or continue with</span>
                    </h5>
                    <ul className="d-flex justify-content-center">
                      <li>
                        <a
                          href=""
                          onClick={(e) =>
                            this.props.accountAccessCallback(
                              e,
                              "facebook_signin"
                            )
                          }
                        >
                          <svg
                            className="facebook-icon"
                            enableBackground="new 0 0 24 24"
                            viewBox="0 0 24 24"
                            xmlns="http://www.w3.org/2000/svg"
                          >
                            <path d="m15.997 3.985h2.191v-3.816c-.378-.052-1.678-.169-3.192-.169-3.159 0-5.323 1.987-5.323 5.639v3.361h-3.486v4.266h3.486v10.734h4.274v-10.733h3.345l.531-4.266h-3.877v-2.939c.001-1.233.333-2.077 2.051-2.077z" />
                          </svg>
                        </a>
                      </li>
                      <li>
                        <a
                          href=""
                          onClick={(e) =>
                            this.props.accountAccessCallback(e, "gmail_signin")
                          }
                        >
                          <svg
                            id="Capa_1"
                            enableBackground="new 0 0 512 512"
                            viewBox="0 0 512 512"
                            xmlns="http://www.w3.org/2000/svg"
                          >
                            <g>
                              <path
                                d="m120 256c0-25.367 6.989-49.13 19.131-69.477v-86.308h-86.308c-34.255 44.488-52.823 98.707-52.823 155.785s18.568 111.297 52.823 155.785h86.308v-86.308c-12.142-20.347-19.131-44.11-19.131-69.477z"
                                fill="#fbbd00"
                              />
                              <path
                                d="m256 392-60 60 60 60c57.079 0 111.297-18.568 155.785-52.823v-86.216h-86.216c-20.525 12.186-44.388 19.039-69.569 19.039z"
                                fill="#0f9d58"
                              />
                              <path
                                d="m139.131 325.477-86.308 86.308c6.782 8.808 14.167 17.243 22.158 25.235 48.352 48.351 112.639 74.98 181.019 74.98v-120c-49.624 0-93.117-26.72-116.869-66.523z"
                                fill="#31aa52"
                              />
                              <path
                                d="m512 256c0-15.575-1.41-31.179-4.192-46.377l-2.251-12.299h-249.557v120h121.452c-11.794 23.461-29.928 42.602-51.884 55.638l86.216 86.216c8.808-6.782 17.243-14.167 25.235-22.158 48.352-48.353 74.981-112.64 74.981-181.02z"
                                fill="#3c79e6"
                              />
                              <path
                                d="m352.167 159.833 10.606 10.606 84.853-84.852-10.606-10.606c-48.352-48.352-112.639-74.981-181.02-74.981l-60 60 60 60c36.326 0 70.479 14.146 96.167 39.833z"
                                fill="#cf2d48"
                              />
                              <path
                                d="m256 120v-120c-68.38 0-132.667 26.629-181.02 74.98-7.991 7.991-15.376 16.426-22.158 25.235l86.308 86.308c23.753-39.803 67.246-66.523 116.87-66.523z"
                                fill="#eb4132"
                              />
                            </g>
                          </svg>
                        </a>
                      </li>
                    </ul>
                    {
                      // Display Error Message when signupError true
                      this.props.signupError &&
                      <div className="alert alert-warning" role="alert">
                        <b>{this.props.signupErrorMessage}</b>
                      </div>
                    }
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default withRouter(SignUp);
