import React, { Component } from 'react';
import './StepTwo.css';
import SVG from 'react-inlinesvg';

import WebsiteView from '../../../commonComponents/WebisiteView/webisiteView';
import { storageCacheKey } from '../../../config/config';
import fetchClient from '../../../helper/fetchClient';

class StepTwo extends Component {
  // constructor(props){
  // super(props);
  // this.state = {};
  // }

  // componentWillMount(){}
  // componentDidMount(){}
  // componentWillUnmount(){}

  // componentWillReceiveProps(){}
  // shouldComponentUpdate(){}
  // componentWillUpdate(){}
  // componentDidUpdate(){}

  constructor(props) {
    super(props);
    this.props.parentCallback(2);
    this.state = {
      widget_icon: '',
      widget_id: '',
      page_number: '2'
    }
  }


  //saving the values in the cache
  saveIntoCache = (key, value) => {
    localStorage.setItem(key, value);
  }

  //retrieve the values from cache
  retrieveFromCache = (key) => {
    return localStorage.getItem(key);
  }

  //validate the cache on pageLoad
  validateCacheData = () => {
    //@todo remove the token addition manually.
    let widget_id = this.state.widget_id;
    console.log("Widget : ", widget_id);
    if (!widget_id) {
      console.log("Widget 2 : ", widget_id);
      widget_id = this.retrieveFromCache(storageCacheKey.WIDGET_KEY);
      if (widget_id) {
        // Found data from cache load the rest of data and hit the apis to update the values
        let widget_icon = this.retrieveFromCache(storageCacheKey.WIDGET_ICON);
        this.setState({
          widget_id: widget_id,
          widget_icon: widget_icon,
        });
      } else {
        // Didn't find the values so keep everything bank and hit apis for creating the widget
        this.setState({ widget_created: true });
        this.setState({
          widget_id: '',
          widget_icon: '',
        });
      }
    }
    console.log("Validation done")
  }

  // Saving state data for this page to the cache
  saveStateDataToCache = () => {
    try {
      this.saveIntoCache(storageCacheKey.WIDGET_ICON, this.state.widget_icon);
      return true;
    } catch (error) {
      return false
    }
  }

  //b10279b3-44dd-2d4d-0870-8498c74d3690

  componentWillMount() {
    // setting up token in the state so that we can make the utilze of it.
    this.validateCacheData();
    console.log("Widget Calls");
    console.log(this.state);
  }

  updateWidget = () => {
    try {

      let data = {
        "icon": {
          "icon_type": "class",
          "icon_url": this.state.widget_icon
        }
      }
      let widgetId = this.state.widget_id;

      console.log(widgetId)

      fetchClient.put(`/services/widgets/step_save/${widgetId}`, data, {}).then((response) => {
        console.log("Updated")
        console.log(response.data);
        if (response.data) {
          let widgetData = response.data;
          if (widgetData.uid && widgetData.hashkey) {
            this.setState({
              widget_created: true,
              widget_id: widgetData.hashkey || '',
              widget_icon: widgetData.icon.icon_url || '',
            })
            if (this.saveStateDataToCache()) {
              if (this.state.widget_created) {
                // redirect to next page
                console.log("Widget Created : Saved");
                this.props.pageRedirect(3);
              } else {
                console.log("Widget Created : Not Saved");
              }
            }
          } else {
            console.log("Widget Created : Failed");
          }
        }

        // then save the data into cache and update the page to next one
      }).catch((error) => {
        console.log("API Error occured.")
        console.log(error);
      })

    } catch (error) {
      console.log("Some  with acios error occured");
      console.log(error)
    }
  }

  setIcon(event) {
    console.log(event.target.value);
    let widgetSelected = event.target.value;
    if (widgetSelected && (widgetSelected == 'option1' || widgetSelected == 'option2' || widgetSelected == 'option3' || widgetSelected == 'option4')) {
      this.setState({
        widget_icon: widgetSelected
      });
    }
  }

  render() {
    return (

      <div className="row">
        <div className="col-lg-12">
          <div className="text-center body-content-area">
            <h1>Now let’s pick an <span>icon</span> for <br />your call widget.</h1>
            <div className="content-area-section m-t-30 ">
              <form className="form-horizontal enter-website custom-form" action="index.html">
                <div className="check-icon c-icon-area-phone-w">
                  <div className="row" onChange={this.setIcon.bind(this)}>

                    <div className="col-sm-6 col-lg-3 col-md-3">
                      <div className="gal-detail thumb">
                        <div className="c-icon-area-phone">
                          <SVG src="/img/icon1.svg" />
                        </div>
                        <div className="radio radio-primary">
                          <input type="radio" name="icon_selector" id="radio1" defaultValue="option1" />
                          <label htmlFor="radio1">
                          </label>
                        </div>
                      </div>
                    </div>

                    <div className="col-sm-6 col-lg-3 col-md-3">
                      <div className="gal-detail thumb">
                        <div className="c-icon-area-phone">
                          <SVG src="/img/icon2.svg" />
                        </div>
                        <div className="radio radio-primary">
                          <input type="radio" name="icon_selector" id="radio2" defaultValue="option2" />
                          <label htmlFor="radio2">
                          </label>
                        </div>
                      </div>
                    </div>

                    <div className="col-sm-6 col-lg-3 col-md-3">
                      <div className="gal-detail thumb">
                        <div className="c-icon-area-phone">
                          <SVG src="/img/icon3.svg" />
                        </div>
                        <div className="radio radio-primary">
                          <input type="radio" name="icon_selector" id="radio3" defaultValue="option3" />
                          <label htmlFor="radio3">
                          </label>
                        </div>
                      </div>
                    </div>

                    <div className="col-sm-6 col-lg-3 col-md-3">
                      <div className="gal-detail thumb">
                        <div className="c-icon-area-phone">
                          <SVG src="/img/icon4.svg" />
                        </div>
                        <div className="radio radio-primary">
                          <input type="radio" name="icon_selector" id="radio4" defaultValue="option4" />
                          <label htmlFor="radio4">
                          </label>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

                <div className="row">
                  <div className="col-sm-12 col-lg-12 col-md-12">
                    <div className="form-group ">
                      <div className="text-center">
                        <a id="confirm" onClick={this.updateWidget} className="btn btn-lg  m-t-20 sign-in do-it btn-botton-line btn-active">I
                          like that</a>
                      </div>
                      <div className="pick-talk">
                        <div className="comment">
                          <img src="assets/images/users/avatar-1.jpg" alt="" className="comment-avatar " />
                          <div className="comment-body">
                            <div className="comment-text">
                              Don't like them? You can change it later. Better yet, call
                              our customer service! They'll make you a special,
                              customwidget. Just the way you want it.
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <WebsiteView />
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>{/* end col */}
      </div>
    );
  }
}

export default StepTwo;
