import React from 'react'
import PropTypes from 'prop-types'

import './styles.css'

class Loading extends React.Component {
  render() {
    const backgroundColor = {
      background: this.props.background
    }
    const loaderColor1 = {
      background: this.props.loaderColor1
    }
    const loaderColor2 = {
      background: this.props.loaderColor2
    }
    const messageLoader = this.props.message;

    if (this.props.loading) {
      return (
        <div className="loading-background" style={backgroundColor}>
          <div className="loading-bar" >
            <div className="loading-circle-1" style={loaderColor1} />
            <div className="loading-circle-2" style={loaderColor2} />
          </div>
          <br></br>
          <div className='loader-text'> <h4>{messageLoader}</h4></div>
        </div>
      )
    }
    return null
  }
}

Loading.defaultProps = {
  loading: false,
  background: 'rgba(236, 240, 241, 0.7)',
  loaderColor: '#e74c3c'
}

Loading.propTypes = {
  loading: PropTypes.bool,
  background: PropTypes.string,
  loaderColor1: PropTypes.string,
  loaderColor2: PropTypes.string,
  message: PropTypes.string,
}

export default Loading
