import React, { Component } from 'react';
import './resetPassword.scss'
import { Form } from 'antd'
import { Auth } from 'aws-amplify';
import CardView from '../../commonComponents/CardView/cardView';
import CustomInput from '../../commonComponents/CustomInput/customInput';
import CustomButton from '../../commonComponents/CustomButton/customButton';
import { passwordRules } from '../../utils/helper';


class ResetPassword extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }


    onResetPswd = (values) => {
        const { oldPassword, newPassword } = values

        Auth.currentAuthenticatedUser().then(user => {
            return Auth.changePassword(user, oldPassword, newPassword);
        }).then(data => {
            console.log(data)
        }).catch(err => {
            console.log(err)
        });
    }

    render() {
        return (

            <CardView>
                <div className="resetPassword-wrapper">
                    <h4 className="sign-heading">
                        <span className="text-in-red">Reset</span> <span className="text-in-black">Password.</span>
                    </h4>

                    <p className="enter-newPswd-text">Enter new password for your account.</p>

                    <div className="signIn-form">
                        <Form
                            name="resetPassword"
                            layout="vertical"
                            onFinish={this.onResetPswd}
                            onFinishFailed={this.onFinishFailed}
                        >
                            <Form.Item
                                label="New Password"
                                name="oldPassword"
                                rules={passwordRules(8)}
                            >
                                <CustomInput typeOfInput="password" placeholder="Atleast 8 characters" />
                            </Form.Item>

                            <Form.Item
                                label="Confirm New Password"
                                name="newPassword"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Please input your password!',
                                    }, ({ getFieldValue }) => ({
                                        validator(rule, value) {
                                            if (!value || getFieldValue('password') === value) {
                                                return Promise.resolve();
                                            }
                                            return Promise.reject('The two passwords that you entered do not match!');
                                        },
                                    })
                                ]}
                            >
                                <CustomInput typeOfInput="password" placeholder="Atleast 8 characters" />
                            </Form.Item>


                            <Form.Item >
                                <CustomButton buttonName="Rest Password" htmlType="submit" />
                            </Form.Item>
                        </Form>
                    </div>
                </div>
            </CardView>
        );
    }
}

export default ResetPassword;